ul li {
    list-style-type: initial;
}

body {
    background-color: rgba(128, 128, 128, 0.5); /* Полупрозрачный серый фон */
}

.app-wrapper {
    width: 800px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
"h h"
"n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
    border-radius: 10px;

}

.app-wrapper-content {

    grid-area: c;
    background-color: cyan;
    border-radius: 10px;
}


